<template>
    <div id="point-view-container">
        <div id="point-view-head">
            <h1 id="point-view-head-title">Point View</h1> 
            <div class="radio-group">
                <input type="radio" id="full-point-view-radio"
                value="full-point" v-model="pointView" name="point-view-radio">
                <label for="full-point-view-radio">All Data</label>
            </div>
            <div class="radio-group">
                <input type="radio" id="skyline-point-view-radio" value="skyline-point" v-model="pointView" name="point-view-radio">
                <label for="skyline-point-view-radio">Skyline Data</label>
            </div>
        </div>
        <div v-show="fullData != null" style="height: 94%">
            <point-view-vis v-show="pointView === 'full-point'" id="full-point-view-vis" :point-data="fullData"></point-view-vis>
            <point-view-vis v-show="pointView === 'skyline-point'" id="skyline-point-view-vis" :point-data="skylineData"></point-view-vis>
        </div>
        <div v-show="fullData === null">
            No data loaded.
        </div>
    </div>
</template>

<script>
import PointViewVis from './point-view-vis';
import DataService from '../../services/data-service';
import PipeService from '../../services/pipe-service';
import SkylineQuery from '../../services/skyline-query';

export default {
    name: 'PointView',
    data() {
        return {
            fullData: null,
            skylineData: null,
            pointView: 'full-point',
        };
    },
    mounted() {
        PipeService.$on(PipeService.SUBATTRIBUTE_UPDATE, (selectedAttrMap) => {
            const attrMap = selectedAttrMap;
            const subspaceData = DataService.genSubspaceData(attrMap);
            const skylineData =
                SkylineQuery.genSkylinePoints(subspaceData.pointTable, attrMap);
            const res = {};
            res.pointTable = skylineData;
            res.attributeTable = Array.from(attrMap.values());
            this.skylineData = res;
        });
        PipeService.$on(PipeService.DATA_CHANGE, () => {
            const data = DataService.getData();
            this.fullData = data;
            const attrMap = new Map();
            data.attributeTable.forEach((attribute) => {
                attrMap.set(attribute.name, attribute);
            });
            this.skylineData = {
                pointTable: SkylineQuery.genSkylinePoints(data.pointTable, attrMap),
                attributeTable: data.attributeTable,
            };
        });
    },
    components: {
        PointViewVis,
    },
};
</script>

<style lang="stylus" scoped>
#point-view-container
    height: 29%
    margin: 5px
#point-view-head
    margin-bottom: 5px
#full-point-view-vis,
#skyline-point-view-vis
    width: 100%
    height: 100%
    overflow-x: auto
    overflow-y: auto
#point-view-head-title
    display: inline-block
.radio-group
    display: inline-block
    margin-left: 10px
label
    margin-bottom: 0
h1
    margin-bottom: 0
</style>



