<template>
    <div id="point-view-canvas" ref="canvas">
    </div>
</template>
<script>
import * as d3 from 'd3';
import d3Tip from 'd3-tip';
// import PipeService from '../../services/pipe-service';
// import DataService from '../../services/data-service';

d3.tip = d3Tip;

export default {
    name: 'PointViewVis',
    data() {
        return {
            data: [],
            selectedAttrMap: undefined,
            canvasWidth: 0,
            DEFAULT_COLUMN_HEIGHT: 20,
            DEFAULT_COLUMN_GAP: 2,
            MAX_COLUMN_WIDTH: 100,
            TABLE_FILLED_COLOR: '#ededed',
        };
    },
    props: {
        pointData: {
            attributeTable: Array,
            pointTable: Array,
        },
    },
    mounted() {
        // PipeService.$on(PipeService.SUBATTRIBUTE_UPDATE, (selectedAttrMap) => {
            // this.selectedAttrMap = selectedAttrMap;
            // this.drawPointView();
        // });
        this.$nextTick(function foo() {
            this.initialize();
        });
    },
    watch: {
        pointData: function bar(newValue) {
            this.setData(newValue);
            this.adjustCanvasSize();
            this.drawPointView();
        },
    },

    methods: {
        initialize() {
            window.addEventListener('resize', () => {
                this.adjustCanvasSize();
            });
        },
        setData(newData) {
            this.data = newData;
            this.selectedAttrMap = new Map();
            this.data.attributeTable.forEach((attribute) => {
                this.selectedAttrMap.set(attribute.name, 'true');
            });
        },

        adjustCanvasSize() {
            const containerWidth = document.querySelector('#point-view-container').clientWidth;
            this.canvasWidth = containerWidth;
            this.setCanvasWidth(this.$refs.canvas, this.canvasWidth);
        },

        initializeCanvas() {
            const el = this.$refs.canvas;
            const height = (this.data.pointTable.length + 1) * this.DEFAULT_COLUMN_HEIGHT;
            const canvasContainer = d3.select(el);
            let canvas = canvasContainer.select('svg');
            if (!canvas.empty()) {
                canvas.remove();
            }
            canvas = canvasContainer.append('svg')
                .attr('width', this.canvasWidth)
                .attr('height', height);
            return canvas;
        },

        sortData(attribute) {
            console.log(attribute);
            const pointTable = this.pointData.pointTable;
            const attr = attribute;
            if (!attribute.sorted) {
                attr.sorted = 1;
            }
            attr.sorted *= -1;
            this.pointData.pointTable = pointTable.sort((a, b) => {
                if (a[attribute.name] > b[attribute.name]) {
                    return -1 * attr.sorted;
                }
                if (a[attribute.name] < b[attribute.name]) {
                    return 1 * attr.sorted;
                }
                return 0;
            });
            console.log(this.pointData.pointTable);
        },

        drawBackgroundTable(canvas) {
            const dataLen = this.selectedAttrMap.size;
            let columnWidth = (this.canvasWidth / dataLen) - this.DEFAULT_COLUMN_GAP;
            columnWidth = Math.min(columnWidth, this.MAX_COLUMN_WIDTH);
            const tableHead = canvas.append('g')
                .attr('class', 'point-view-table-head');
            const attributeTable = this.data.attributeTable.filter(attribute =>
                this.selectedAttrMap.has(attribute.name)
            );
            tableHead.selectAll('.point-viewtable-head-cell')
                .data(attributeTable)
                .enter()
                .append('rect')
                .attr('class', 'point-view-table-head-cell')
                .attr('width', columnWidth)
                .attr('height', this.DEFAULT_COLUMN_HEIGHT)
                .attr('x', (d, i) => i * (columnWidth + this.DEFAULT_COLUMN_GAP))
                .attr('y', 0)
                .attr('fill', this.TABLE_FILLED_COLOR)
                .on('click', (d) => {
                    this.sortData(d);
                });

            this.data.pointTable.forEach((point, index) => {
                const tableBody = canvas.append('g')
                    .attr('class', 'point-view-table-body');
                tableBody.selectAll('.point-view-table-body-cell')
                    .data(attributeTable)
                    .enter()
                    .append('rect')
                    .attr('class', 'point-view-table-body-cell')
                    .attr('width', columnWidth)
                    .attr('height', this.DEFAULT_COLUMN_HEIGHT)
                    .attr('x', (d, i) => i * (columnWidth + this.DEFAULT_COLUMN_GAP))
                    .attr('y', () => (index + 1) * this.DEFAULT_COLUMN_HEIGHT)
                    .attr('fill', () => {
                        if (index % 2 === 0) {
                            return '#ffffff';
                        }
                        return this.TABLE_FILLED_COLOR;
                    });
            });
        },

        drawTableData(canvas) {
            const tip = d3.tip().attr('class', 'd3-tip').html(d => `<div class="d3-tip">${d.name}: ${d.value}</div>`);
            canvas.call(tip);
            const dataLen = this.selectedAttrMap.size;
            let columnWidth = (this.canvasWidth / dataLen) - this.DEFAULT_COLUMN_GAP;
            columnWidth = Math.min(columnWidth, this.MAX_COLUMN_WIDTH);
            const attributeTable = this.data.attributeTable.filter(attribute =>
                this.selectedAttrMap.has(attribute.name)
            );
            const tableHead = canvas.append('g')
                .attr('class', 'point-view-data-head');
            tableHead.selectAll('.point-view-data-head-value')
                .data(attributeTable)
                .enter()
                .append('text')
                .text(d => d.name)
                .attr('class', 'point-view-data-head-value')
                .attr('text-anchor', 'middle')
                .attr('alignment-baseline', 'central')
                .attr('x', (d, i) => (i * (columnWidth + this.DEFAULT_COLUMN_GAP)) + (columnWidth / 2))
                .attr('y', this.DEFAULT_COLUMN_HEIGHT / 2);

            this.data.pointTable.forEach((point, index) => {
                const tableBody = canvas.append('g')
                    .attr('class', 'point-view-data-body');
                attributeTable.forEach((attribute, attrIndex) => {
                    if (attribute.type === 'nominal') {
                        tableBody.append('text')
                            .text(point[attribute.name])
                            .attr('class', 'point-view-data-body-value')
                            .attr('text-anchor', 'middle')
                            .attr('alignment-baseline', 'central')
                            .attr('x', (attrIndex * (columnWidth + this.DEFAULT_COLUMN_GAP)) + (columnWidth / 2))
                            .attr('y', (index + 1.5) * this.DEFAULT_COLUMN_HEIGHT);
                    } else if (attribute.type === 'ordinal') {
                        tableBody.append('rect')
                            .attr('class', 'point-view-data-body-value')
                            .attr('width', (point[attribute.name] / attribute.maxValue) * columnWidth)
                            .attr('height', this.DEFAULT_COLUMN_HEIGHT - 4)
                            .attr('x', attrIndex * (columnWidth + this.DEFAULT_COLUMN_GAP))
                            .attr('y', () => ((index + 1) * this.DEFAULT_COLUMN_HEIGHT) + 2)
                            .attr('fill', 'steelblue')
                            .on('mouseover', function foo() {
                                d3.select(this).attr('fill', '#B0171F');
                                const tipContent = {};
                                tipContent.name = attribute.name;
                                tipContent.value = point[attribute.name];
                                tip.show(tipContent);
                            })
                            .on('mouseout', function foo() {
                                d3.select(this).attr('fill', 'steelblue');
                                tip.hide();
                            });
                    }
                });
            });
        },

        drawPointView() {
            const canvas = this.initializeCanvas();
            this.drawBackgroundTable(canvas);
            this.drawTableData(canvas);
        },

        setCanvasWidth(el, width) {
            const canvasContainer = d3.select(el);
            const canvas = canvasContainer.select('svg');
            if (!canvas.empty()) {
                canvas.attr('width', width);
            }
        },
    },
};
</script>

<style lang="stylus">
.point-view-table-head
    cursor: pointer

.point-view-data-body-value
    font-size: 12px

.point-view-data-head-value
    font-size: 12px
    font-weight: bold

.d3-tip
    text-align: center 
    min-width: 50px
    padding-top: 2px
    padding-bottom: 2px
    padding-left: 4px
    padding-right: 4px
    background-color: #3a3a3c 
    color: #ffffff 
    border: 1px solid #3a3a3c 
    border-radius: 5px
    font-size: 12px
</style>



