function isDominatingValue(vx, vy, attribute) {
    let res = 0;
    if (attribute.type === 'ordinal') {
        if (vx !== vy) {
            switch (attribute.order) {
                case 'ascending': {
                    res = vx < vy ? 1 : -1;
                    break;
                }
                case 'descending': {
                    res = vx > vy ? 1 : -1;
                    break;
                }
                default: {
                    res = vx > vy ? 1 : -1;
                }
            }
        }
    }

    return res;
}

/*
 * px dominates py: return 1
 * py domiantes px: return -1
 * otherwise return 0
 */
function isDominatingPoint(px, py, attributeTable) {
    let xDominateFlag = false;
    let yDominateFlag = false;
    Object.keys(px).forEach((key) => {
        const valueX = px[key];
        const valueY = py[key];
        if (isDominatingValue(valueX, valueY, attributeTable.get(key)) === 1) {
            xDominateFlag = true;
        }

        if (isDominatingValue(valueX, valueY, attributeTable.get(key)) === -1) {
            yDominateFlag = true;
        }
        // OPTIMIZE: if xDominateFlag && yDominateFlag then break
    });

    if (xDominateFlag && !yDominateFlag) {
        return 1;
    }

    if (!xDominateFlag && yDominateFlag) {
        return -1;
    }

    return 0;
}

/*
 * Return: a skyeline point Set
 */
function genSkylinePoints(points, attributeTable) {
    const skylinePoints = new Set();
    points.forEach((p) => {
        let isSkylinePoint = true;
        skylinePoints.forEach((candidate) => {
            const isDominating = isDominatingPoint(p, candidate, attributeTable);
            // p dominates candidate
            if (isDominating === 1) {
                // seems safe to delete during iteration acccording to ES6's document
                skylinePoints.delete(candidate);
            }

            // candidate dominates p
            if (isDominating === -1) {
                isSkylinePoint = false;
                // OPTIMIZE: break
            }
        });

        if (isSkylinePoint) {
            skylinePoints.add(p);
        }
    });

    return Array.from(skylinePoints);
}

function genDefaultOrdinalAttributeTable(points) {
    const attributeTable = new Map();
    if (points.length !== 0) {
        const p = points[0];
        Object.keys(p).forEach((key) => {
            const attributeConfig = {};
            attributeConfig.name = key;
            attributeConfig.type = 'ordinal';
            attributeConfig.order = 'ascending';
            attributeTable.set(key, attributeConfig);
        });
    }

    return attributeTable;
}

export default {
    genSkylinePoints,
    genDefaultOrdinalAttributeTable,
};




