<template>
    <div id="attr-list" class="control-section">
        <div class="control-section-head">
            <h1 class="control-section-title">Attribute Table</h1>
            <button type="button" class="btn btn-outline-primary btn-sm
                control-btn" v-if="attributeTable.length > 0" @click="subattributeUpdate">Update Skyline</button>
        </div>
        <div id="table-view" :style="{'max-height': setting.height * 0.57 + 'px'}"  v-if="attributeTable.length > 0">
            <table>
                <thead>
                    <tr @click="selectAllAttr()" v-on:dblclick="deselectAllAttr()">
                        <th>Attribute Name</th>
                        <th>Attribute Type</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for='attribute in attributeTable' :class="{selected: attribute.selected}" @click="selectAttr(attribute)">
                        <td>{{attribute.name}}</td>
                        <td>{{attribute.type}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div v-else>
            No data loaded.
        </div>
    </div>
</template>

<script>
import PipeService from '../../services/pipe-service';
import DataService from '../../services/data-service';

export default {
    name: 'AttrList',
    mounted() {
        PipeService.$on(PipeService.DATA_CHANGE, () => {
            this.attributeTable = DataService.getData().attributeTable;
        });
    },
    data() {
        return {
            attributeTable: [],
        };
    },
    props: {
        setting: Object,
    },
    methods: {
        selectAttr(attribute) {
            const attr = attribute;
            if (!attr.selected) {
                this.$set(attr, 'selected', true);
            } else {
                this.$set(attr, 'selected', false);
            }
        },

        selectAllAttr() {
            this.attributeTable.forEach(attr => this.$set(attr, 'selected', true));
        },

        deselectAllAttr() {
            this.attributeTable.forEach(attr => this.$set(attr, 'selected', false));
        },

        subattributeUpdate() {
            const selectedAttrMap = new Map();
            this.attributeTable.forEach((attr) => {
                if (attr.selected) {
                    selectedAttrMap.set(attr.name, attr);
                }
            });
            PipeService.$emit(PipeService.SUBATTRIBUTE_UPDATE, selectedAttrMap);
        },
    },

};
</script>

<style lang="stylus" scoped>

@import './control-panel.styl'

#table-view
    overflow-y: auto
    margin-bottom: 5px

#table-view table
    width: 100%
    table-layout: fixed
    font-size: small
    vertical-align: middle
    text-align: center

#table-view table thead tr th 
    width: 100%
    border-top: 1px solid rgba(0, 0, 0, 0.1)
    border-bottom: 1px solid rgba(0, 0, 0, 0.1)
    border-left: 1px solid rgba(0, 0, 0, 0.1)
    text-align: center
    font-weight: bold
    cursor: pointer
    -webkit-user-select: none /* Chrome/Safari */        
    -moz-user-select: none /* Firefox */
    -ms-user-select: none /* IE10+ */

#table-view table thead tr th:last-child
    border-right: 1px solid rgba(0, 0, 0, 0.1)

#table-view table tbody tr td
    border-bottom: 1px solid rgba(0, 0, 0, 0.1)
    border-left: 1px solid rgba(0, 0, 0, 0.1)
    cursor: pointer
    -webkit-user-select: none /* Chrome/Safari */        
    -moz-user-select: none /* Firefox */
    -ms-user-select: none /* IE10+ */

#table-view table tbody tr:hover
    font-weight: bold

#table-view table tbody tr td:last-child
    border-right: 1px solid rgba(0, 0, 0, 0.1)

</style>



